import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddEnquiryComponent } from './add-enquiry.component';
import { AddSelfEnquiryComponent } from './add-self-enquiry/add-self-enquiry.component';
import { AddKidsEnquiryComponent } from './add-kids-enquiry/add-kids-enquiry.component';

const routes: Routes = [
  { path: '', component: AddEnquiryComponent, children: [
    { path: '', redirectTo: 'kids', pathMatch: 'full' },
    { path: 'self', component: AddSelfEnquiryComponent },
    { path: 'kids', component: AddKidsEnquiryComponent }
  ]
}
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddEnquiryRoutingModule { }
